html {
  color-scheme: light dark;
  color: #64484b;
  background-color: #c49399;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-family: Trebuchet MS, Lucida Sans Unicode, Lucida Grande, Lucida Sans, Arial, sans-serif;
  font-size: larger;
  display: flex;
}

a {
  color: #333;
  font-size: 1.2em;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s, text-decoration .3s;
}

a:hover {
  color: #70315b;
  text-decoration: underline;
}

a:visited {
  color: #555;
}

a:active {
  color: #70315b;
}

.infobox {
  text-align: center;
  box-sizing: border-box;
  background-color: #cca1a6;
  flex-grow: 1;
  flex-basis: calc(50% - 20px);
  min-width: 300px;
  margin: 10px;
  padding: 10px;
}

.surroundBox {
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 5% 6em;
  display: flex;
}

.boldCenter {
  margin-top: 2em;
  font-weight: bolder;
}

.centerPadding {
  padding: 2.5vh;
}

.inlineLeft {
  text-align: left;
  display: inline-block;
}

.header {
  border-bottom: 3px solid #000;
  justify-content: space-between;
  align-items: center;
  max-width: 55%;
  margin-top: 2em;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
  display: flex;
}

.header-images-row {
  justify-content: space-between;
  width: 100%;
  display: flex;
}

.header-image {
  object-fit: contain;
  width: 18%;
  height: auto;
}

.header-center {
  color: #292929;
  text-align: center;
  flex-grow: 1;
  order: 1;
  padding: 20px 0;
}

.header-nimet {
  width: 55%;
  height: auto;
  margin-top: 10vh;
}

.left-image {
  order: 0;
  margin-top: 8vh;
}

.right-image {
  order: 2;
  margin-top: 8vh;
}

.header-date, .header-event {
  margin: 0;
  font-size: 2vh;
}

@media (width <= 1600px) {
  .header {
    max-width: 70%;
  }
}

@media (width <= 1200px) {
  .header {
    flex-flow: row;
    max-width: 75%;
    margin-top: 2vh;
  }

  .header-images-row {
    justify-content: space-between;
    width: 100%;
    display: flex;
  }

  .header-image {
    object-fit: contain;
    width: 23%;
    height: auto;
  }

  .header-nimet {
    width: 60%;
    height: auto;
  }

  .header-date, .header-event {
    font-size: 2vh;
  }

  .surroundBox {
    margin-left: 2.5%;
    margin-right: 2.5%;
  }
}

@media (width <= 800px) {
  .header {
    flex-flow: row;
    max-width: 100%;
    margin-top: 2vh;
  }

  .header-image {
    object-fit: contain;
    width: 20%;
    height: auto;
  }

  .header-nimet {
    width: 60%;
    height: auto;
  }

  .header-images-row {
    margin-top: 20vh;
  }

  .header-date, .header-event {
    font-size: 1.2em;
  }

  .surroundBox {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    min-height: 50vh;
    margin-left: .5%;
    margin-right: .5%;
    display: flex;
  }

  .infobox {
    width: 90%;
  }
}

@media (width <= 550px) {
  a {
    color: #333;
    font-size: 1.4em;
    font-weight: bold;
    text-decoration: none;
    transition: color .3s, text-decoration .3s;
  }

  .header {
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    margin-top: 25vh;
    padding-left: .5vh;
    padding-right: .5vh;
  }

  .header-center {
    order: 2;
  }

  .header-nimet {
    order: 2;
    min-width: 95%;
    max-width: 100%;
    margin-top: 0;
    margin-left: .7vh;
    margin-right: .7vh;
  }

  .header-images-row {
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    margin-top: 0;
    display: flex;
  }

  .left-image {
    order: 0;
    margin-top: 25vh;
    margin-left: 10%;
  }

  .right-image {
    order: 1;
    margin-top: 25vh;
    margin-right: 10%;
  }

  .left-image, .right-image {
    width: 40%;
    height: auto;
  }
}
